<template>
  <div
    class="form max-w-2xl border bg-cyan-900 bg-opacity-10 border-cyan-700 mx-auto rounded-sm"
  >
    <h1 class="mt-5 text-3xl text-center text-teal-300">Find your phone</h1>
    <div class="p-5 pt-3">
      <details class="block" open>
        <summary>General</summary>
        <div class="content">
          <SelectionComponent
            :name="'Brand'"
            :options="[
              'Samsung',
              'Huawei',
              'Xiaomi',
              'Apple',
              'OnePlus',
              'Oppo',
              'LG',
              'HTC',
            ]"
            v-model="parameters.brands"
          />

          <div class="my-3 text-cyan-300">
            <p class="text-lg mb-1">Release year</p>
            <div class="flex space-x-2">
              <input
                class="border border-cyan-300"
                type="number"
                placeholder="Minimum year"
                v-model="parameters.yearMin"
              />
              <span class="text-xl uppercase leading-10">-</span>
              <input
                class="border border-cyan-300"
                type="number"
                placeholder="Maximum year"
                v-model="parameters.yearMax"
              />
            </div>
          </div>

          <div class="my-3 text-cyan-300">
            <p class="text-lg mb-1">Price (€)</p>
            <div class="flex space-x-2">
              <input
                class="border border-cyan-300"
                type="number"
                placeholder="Minimum price"
                v-model="parameters.priceMin"
              />
              <span class="text-xl uppercase leading-10">-</span>
              <input
                class="border border-cyan-300"
                type="number"
                placeholder="Maximum price"
                v-model="parameters.priceMax"
              />
            </div>
          </div>
        </div>
      </details>

      <details>
        <summary>Specs</summary>
        <SelectionComponent
          :name="'OS'"
          :options="['Android', 'iOS', 'Windows']"
          v-model="parameters.os"
        />
        <SelectionComponent
          :name="'5G'"
          :options="['5G']"
          v-model="parameters.fiveg"
        />
        <div class="content">
          <div class="my-3 text-cyan-300">
            <p class="text-lg mb-1">CPU Frequency (MHz)</p>
            <div class="flex space-x-2">
              <input
                class="border border-cyan-300"
                type="number"
                placeholder="Minimum frequency"
                v-model="parameters.cpuMin"
              />
              <span class="text-xl uppercase leading-10">-</span>
              <input
                class="border border-cyan-300"
                type="number"
                placeholder="Maximum frequency"
                v-model="parameters.cpuMax"
              />
            </div>
          </div>

          <div class="my-3 text-cyan-300">
            <p class="text-lg mb-1">CPU Cores</p>
            <div class="flex space-x-2">
              <input
                class="border border-cyan-300"
                type="number"
                placeholder="Minimum cores"
                v-model="parameters.cpuCoresMin"
              />
              <span class="text-xl uppercase leading-10">-</span>
              <input
                class="border border-cyan-300"
                type="number"
                placeholder="Maximum cores"
                v-model="parameters.cpuCoresMax"
              />
            </div>
          </div>

          <div class="my-3 text-cyan-300">
            <p class="text-lg mb-1">RAM (GB)</p>
            <div class="flex space-x-2">
              <input
                class="border border-cyan-300"
                type="number"
                placeholder="Minimum RAM"
                v-model="parameters.ramMin"
              />
              <span class="text-xl uppercase leading-10">-</span>
              <input
                class="border border-cyan-300"
                type="number"
                placeholder="Maximum RAM"
                v-model="parameters.ramMax"
              />
            </div>
          </div>

          <div class="my-3 text-cyan-300">
            <p class="text-lg mb-1">Storage (GB)</p>
            <div class="flex space-x-2">
              <input
                class="border border-cyan-300"
                type="number"
                placeholder="Minimum storage"
                v-model="parameters.storageMin"
              />
              <span class="text-xl uppercase leading-10">-</span>
              <input
                class="border border-cyan-300"
                type="number"
                placeholder="Maximum storage"
                v-model="parameters.storageMax"
              />
            </div>
          </div>
          <RadioComponent
            :name="'Resolution'"
            :options="[
              '240x320',
              '240x400',
              '320x480',
              '480x800',
              '540x960',
              '720x1280',
              '1080x1920',
              '1440x2560',
              '1644x3840',
            ]"
            v-model="parameters.resolution"
          />
        </div>
      </details>
      <RadioComponent
        :name="'Order'"
        :options="[
          'Popularity',
          'Price',
          'Weight',
          'Camera Resolution',
          'Battery Capacity',
        ]"
        v-model="parameters.order"
      />
      <router-link
        :to="path"
        class="p-2 my-5 block text-center w-full text-teal-300 border border-teal-300 text-lg uppercase hover:border-teal-600 hover:bg-teal-600 hover:text-white transition duration-300"
      >
        Search
      </router-link>
    </div>
  </div>
</template>

<script>
import SelectionComponent from "@/components/SelectionComponent";
import RadioComponent from "@/components/RadioComponent";

export default {
  name: "SearchView",
  components: {
    SelectionComponent,
    RadioComponent,
  },
  data() {
    return {
      parameters: {
        brands: [],
        yearMin: "",
        yearMax: "",
        priceMin: "",
        priceMax: "",
        os: [],
        fiveg: "",
        cpuMin: "",
        cpuMax: "",
        cpuCoresMin: "",
        cpuCoresMax: "",
        ramMin: "",
        ramMax: "",
        storageMin: "",
        storageMax: "",
        resolution: "",
        order: "",
      },
      path: "/results?",
    };
  },
  methods: {
    updatePath() {
      let path = "/results?";
      const params = this.parameters;

      if (params.brands != "") {
        path += "brands=" + params.brands + "&";
      }
      if (params.yearMin != "") {
        path += "yearMin=" + params.yearMin + "&";
      }
      if (params.yearMax != "") {
        path += "yearMax=" + params.yearMax + "&";
      }
      if (params.priceMin != "") {
        path += "priceMin=" + params.priceMin + "&";
      }
      if (params.priceMax != "") {
        path += "priceMax=" + params.priceMax + "&";
      }
      if (params.os != "") {
        path += "os=" + params.os + "&";
      }
      if (params.fiveg != "") {
        path += "fiveg=" + params.fiveg + "&";
      }
      if (params.cpuMin != "") {
        path += "cpuMin=" + params.cpuMin + "&";
      }
      if (params.cpuMax != "") {
        path += "cpuMax=" + params.cpuMax + "&";
      }
      if (params.cpuCoresMin != "") {
        path += "cpuCoresMin=" + params.cpuCoresMin + "&";
      }
      if (params.cpuCoresMax != "") {
        path += "cpuCoresMax=" + params.cpuCoresMax + "&";
      }
      if (params.ramMin != "") {
        path += "ramMin=" + params.ramMin + "&";
      }
      if (params.ramMax != "") {
        path += "ramMax=" + params.ramMax + "&";
      }
      if (params.storageMin != "") {
        path += "storageMin=" + params.storageMin + "&";
      }
      if (params.storageMax != "") {
        path += "storageMax=" + params.storageMax + "&";
      }
      if (params.resolution != "") {
        path += "resolution=" + params.resolution + "&";
      }
      if (params.order != "") {
        path += "order=" + params.order + "&";
      }
      if (path.slice(-1) == "&") {
        this.path = path.slice(0, -1);
      } else {
        this.path = path;
      }
    },
  },
  watch: {
    parameters: {
      handler() {
        this.updatePath();
      },
      deep: true,
    },
  },
};
</script>

<style scoped>
h2,
summary {
  @apply mt-5 text-2xl text-teal-300 uppercase cursor-pointer;
}

label {
  display: block;
  @apply text-lg mb-2;
}

.input-container {
  @apply my-1 rounded-sm  flex;
}

.icon-container {
  @apply p-2 px-3 text-lg bg-cyan-600;
}

input {
  @apply focus:outline-none p-2 text-lg w-full bg-slate-800;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}
</style>
