<template>
  <AdminNavigation />

  <table class="w-full my-5" v-if="phones">
    <tr>
      <th>Phone</th>
      <th>Wishes</th>
      <th>Stock</th>
    </tr>
    <tr v-for="(phone, index) in phones" :key="index">
      <td>{{ phone.fullname }}</td>
      <td>{{ phone.wishes.length }}</td>
      <td
        :class="{
          'text-red-500': phone.stock < phone.wishes.length,
          'text-green-500': phone.stock > phone.wishes.length,
          'text-cyan-500': phone.stock == phone.wishes.length,
        }"
      >
        {{ phone.stock }}
      </td>
    </tr>
  </table>
</template>

<script>
import phones_controller from "../../controllers/phones";
import { mapGetters } from "vuex";
import router from "../router";
import AdminNavigation from "../components/AdminNavigationComponent.vue";

export default {
  name: "WishesView",
  components: {
    AdminNavigation,
  },
  computed: {
    ...mapGetters({
      authenticated: "auth/authenticated",
      user: "auth/user",
      isAdmin: "auth/isAdmin",
    }),
  },
  data() {
    return {
      phones: null,
      error: "",
    };
  },
  methods: {
    async getWishes() {
      try {
        this.phones = await phones_controller.getAllWishes();
      } catch (error) {
        this.error = error;
      }
    },
  },
  beforeMount() {
    if (!this.isAdmin) {
      router.push("/login");
    }

    this.getWishes();
  },
};
</script>

<style scoped>
table {
  @apply text-left;
}

th,
td {
  @apply border border-cyan-600 border-opacity-80 p-2 bg-slate-900;
}

th {
  @apply bg-opacity-80;
}

td {
  @apply bg-opacity-20;
}
</style>
