<template>
  <h1
    class="text-2xl text-center text-cyan-300 pb-5 mb-5 border-b border-cyan-300"
  >
    Add Stock
  </h1>
  <label for="file" class="block mb-2">Choose Excel file:</label>
  <input type="file" id="file" @change="setFile" />
  <table class="w-full my-5" v-if="products">
    <tr>
      <th>Phone</th>
      <th>Price</th>
      <th>Quantity</th>
    </tr>
    <tr v-for="(product, index) in products" :key="index">
      <td>{{ product.fullname }}</td>
      <td>{{ product.price }}€</td>
      <td>{{ product.quantity }}</td>
    </tr>
  </table>
  <button
    class="my-5 py-2 px-3 border border-cyan-300 text-cyan-300 uppercase text-lg hover:text-cyan-500 hover:border-cyan-500 transition-all duration-100"
    v-if="products"
    @click="upload"
  >
    Import into database
  </button>
  <p class="error" v-if="error != ''">{{ error }}</p>
</template>

<script>
import stock_controller from "../../controllers/stock";
const XLSX = require("xlsx");

import { mapGetters } from "vuex";
import router from "../router";

export default {
  name: "StockAddView",
  components: {},
  computed: {
    ...mapGetters({
      authenticated: "auth/authenticated",
      user: "auth/user",
      isAdmin: "auth/isAdmin",
    }),
  },
  data() {
    return {
      file: null,
      result: null,
      products: null,
      error: "",
    };
  },
  methods: {
    setFile(event) {
      const reg = /([a-zA-Z0-9\s_\\.])+(.xlsx)$/i;
      const file = event.target.files[0];

      let error = false;

      if (file) {
        if (!reg.exec(file.name)) {
          this.error = "File must be .xlsx";
          error = true;
        }
        if (error) {
          this.file = null;
          this.products = null;
          return;
        } else {
          this.file = file;

          this.parseFile();
        }
      }
    },
    parseFile() {
      const processFile = (file) => {
        const reader = new FileReader();

        return new Promise((resolve, reject) => {
          reader.onload = (e) => {
            const data = new Uint8Array(e.target.result);
            const workbook = XLSX.read(data, { type: "array" });
            let sheetName = workbook.SheetNames[0];
            let worksheet = workbook.Sheets[sheetName];

            const json = XLSX.utils.sheet_to_json(worksheet);
            console.log(json);
            resolve(json);
          };

          reader.onerror = () => {
            reject("error");
          };
          reader.readAsArrayBuffer(file);
        });
      };

      processFile(this.file).then((json) => {
        this.products = json;
      });
    },

    async upload() {
      try {
        await stock_controller.addStock(this.products);
        router.push("/admin/stock");
      } catch (error) {
        this.error = error;
      }
    },
  },
  beforeMount() {
    if (!this.isAdmin) {
      router.push("/login");
    }
  },
};
</script>

<style scoped>
table {
  @apply text-left;
}

th,
td {
  @apply border border-cyan-600 border-opacity-80 p-2 bg-slate-900;
}

th {
  @apply bg-opacity-80;
}

td {
  @apply bg-opacity-20;
}

.error {
  @apply text-red-500 my-5;
}
</style>
