<template>
  <header
    class="bg-cyan-900 bg-opacity-10 border-b border-cyan-800 border-opacity-50"
  >
    <div
      class="container max-w-6xl mx-auto p-6 flex flex-row justify-between flex-wrap content-center"
    >
      <div class=" ">
        <router-link to="/">
          <span class="text-4xl hover:text-white transition-colors duration-75"
            >MyPhoneStore</span
          >
        </router-link>
      </div>
      <div class="flex content-center">
        <template v-if="!authenticated">
          <router-link to="/login">
            <button class="account-link">
              <i class="fas fa-sign-in-alt"></i> Sign in
            </button>
          </router-link>
          <router-link to="/register">
            <button class="account-btn">
              <i class="fas fa-user-plus"></i> Sign up
            </button>
          </router-link>
        </template>

        <template v-else>
          <div class="dropdown">
            <button
              :class="{ 'dropdown-button-active': dropdown }"
              class="dropdown-button welcome-link"
              @click="toggleDropdown"
            >
              Welcome, {{ user.username }}!
            </button>
            <div v-if="dropdown" class="dropdown-content">
              <ul>
                <router-link to="/profile" @click="toggleDropdown">
                  <li><i class="fas fa-user-circle"></i>My profile</li>
                </router-link>
                <router-link to="/admin" v-if="isAdmin" @click="toggleDropdown">
                  <li><i class="fas fa-lock"></i>Admin Panel</li>
                </router-link>
                <router-link to="/myphones">
                  <li><i class="fas fa-mobile-alt"></i>My phones</li>
                </router-link>
                <li @click="logout">
                  <i class="fas fa-sign-out-alt"></i>Sign out
                </li>
              </ul>
            </div>
          </div>
        </template>
      </div>
    </div>
  </header>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import router from "../router";

export default {
  name: "HeaderComponent",
  data() {
    return {
      dropdown: false,
    };
  },
  computed: {
    ...mapGetters({
      authenticated: "auth/authenticated",
      user: "auth/user",
      isAdmin: "auth/isAdmin",
    }),
  },
  methods: {
    ...mapActions({
      logoutAction: "auth/logout",
    }),

    logout() {
      this.toggleDropdown();
      this.logoutAction();
      router.push("/");
    },

    toggleDropdown() {
      this.dropdown = !this.dropdown;
    },
  },
};
</script>

<style scoped>
i {
  @apply w-7 text-center mr-2;
}

.account-btn {
  @apply p-2 mx-2 text-lg rounded-sm border border-teal-400 text-teal-400 hover:border-teal-600 hover:bg-teal-600 hover:text-white transition duration-300;
}

.account-link {
  @apply p-2 mx-2 text-xl text-cyan-400 hover:text-cyan-500 transition-all;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-button {
  @apply p-2 pr-5 mx-2 text-xl text-cyan-400 hover:text-cyan-500 transition-all;
  position: relative;
}

.dropdown-button::after {
  content: "";
  border-top: 6px solid rgb(34, 211, 238);
  border-right: 6px solid transparent;
  border-bottom: 0 dotted;
  border-left: 6px solid transparent;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  transition: all;
  transition-duration: 100ms;
}

.dropdown-button-active::after {
  transform: rotate(180deg);
}

.dropdown-content {
  position: absolute;
  @apply mt-3 bg-slate-900 bg-opacity-100 w-60 border border-slate-600 shadow-md z-50;
}

.dropdown-content li {
  @apply p-3 text-lg hover:text-cyan-300 hover:bg-black hover:bg-opacity-50 transition-all duration-150 cursor-pointer;
}
</style>
