<template>
  <div
    class="form p-5 max-w-2xl border bg-cyan-900 bg-opacity-10 border-cyan-700 mx-auto rounded-sm"
  >
    <h1 class="text-3xl mb-5 text-center text-cyan-300">My profile</h1>
    <h2>
      Username: <span class="text-gray-200">{{ user.username }}</span>
    </h2>
    <h2>
      Email: <span class="text-gray-200">{{ user.email }}</span>
    </h2>
    <h2>
      Verified: <span v-if="user.isActive" class="text-green-500">Yes</span
      ><span v-else class="text-red-500"
        >No
        <button
          @click="verify"
          class="p-1 px-2 float-right border border-red-500 hover:bg-red-500 hover:text-gray-200 transition-all duration-100 uppercase"
        >
          Verify now
        </button></span
      >
    </h2>
    <p v-if="notice != ''" class="text-gray-200 text-center text-lg mt-5">
      {{ notice }}
    </p>
    <p v-if="error != ''" class="text-red-500 text-center text-lg mt-5">
      {{ error }}
    </p>
    <div class="profile-buttons">
      <router-link to="/">
        <button>Home</button>
      </router-link>
      <router-link to="/myphones">
        <button>Phones</button>
      </router-link>
      <router-link to="/profile/edit">
        <button>Edit</button>
      </router-link>
      <button @click="logout">Logout</button>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import UsersController from "../../controllers/users.js";
import router from "../router";

export default {
  name: "ProfileView",
  components: {},
  data() {
    return {
      user: {},
      error: "",
      notice: "",
    };
  },
  methods: {
    ...mapActions({
      logoutAction: "auth/logout",
    }),

    async getUser() {
      try {
        this.user = await UsersController.getUserDetails();
      } catch (error) {
        this.error = error;
      }
    },

    async verify() {
      try {
        const response = await UsersController.sendVerificationEmail();
        this.notice = response;
      } catch (error) {
        this.error = error;
      }
    },

    logout() {
      this.logoutAction();
      router.push("/");
    },
  },
  beforeMount() {
    this.getUser();
  },
};
</script>

<style scoped>
h2 {
  word-break: break-all;
}

h2 {
  @apply text-xl text-teal-300 mb-3;
}

.profile-buttons {
  @apply flex flex-row flex-wrap justify-between mt-10;
}

.profile-buttons button {
  @apply w-32 m-2 text-center uppercase text-lg py-2 px-3 border border-teal-300 text-teal-300 hover:scale-105 hover:border-teal-500 hover:text-teal-500 transition-all duration-200;
}
</style>
