import api from "../service/baseService";

class PhonesController {
  static async getPhones(params) {
    try {
      const response = await api.post("/scraper/search", { params: params });

      return response.data;
    } catch (error) {
      return error;
    }
  }

  static async getPhoneDetails(phone) {
    try {
      const response = await api.get("/scraper/phone/" + phone);

      return response.data;
    } catch (error) {
      return error;
    }
  }

  static async getProductStock(name) {
    try {
      const response = await api.get("/stock/phone/" + name);

      return response.data;
    } catch (error) {
      return error;
    }
  }

  static async checkWish(phoneID) {
    try {
      const response = await api.get("/phones/wish/" + phoneID);

      return response.data;
    } catch (error) {
      return error;
    }
  }

  static async addWish(phoneID) {
    try {
      const response = await api.post("/phones/wish", { phoneID: phoneID });

      return response.data;
    } catch (error) {
      return error;
    }
  }

  static async removeWish(phoneID) {
    try {
      const response = await api.delete("/phones/wish/" + phoneID);

      return response.data;
    } catch (error) {
      return error;
    }
  }

  static async getWishes() {
    try {
      const response = await api.get("/phones/wishlist");

      return response.data;
    } catch (error) {
      return error;
    }
  }

  static async getAllWishes() {
    try {
      const response = await api.get("/phones/wishes");

      return response.data;
    } catch (error) {
      return error;
    }
  }
}

export default PhonesController;
