<template>
  <AdminNavigation />
  <router-link
    to="/admin/stock/add"
    class="text-cyan-400 text-lg hover:underline"
  >
    Add Stock
  </router-link>

  <table class="w-full my-5" v-if="phones">
    <tr>
      <th>Phone</th>
      <th>Price</th>
      <th>Quantity</th>
    </tr>
    <tr v-for="(phone, index) in phones" :key="index">
      <td>{{ phone.fullname }}</td>
      <td>{{ phone.price }}€</td>
      <td>{{ phone.stock }}</td>
    </tr>
  </table>
</template>

<script>
import stock_controller from "../../controllers/stock";
import { mapGetters } from "vuex";
import router from "../router";
import AdminNavigation from "../components/AdminNavigationComponent.vue";

export default {
  name: "StockView",
  components: {
    AdminNavigation,
  },
  computed: {
    ...mapGetters({
      authenticated: "auth/authenticated",
      user: "auth/user",
      isAdmin: "auth/isAdmin",
    }),
  },
  data() {
    return {
      phones: null,
      error: "",
    };
  },
  methods: {
    async getStock() {
      try {
        this.phones = await stock_controller.getStock();
      } catch (error) {
        this.error = error;
      }
    },
  },
  beforeMount() {
    if (!this.isAdmin) {
      router.push("/login");
    }

    this.getStock();
  },
};
</script>

<style scoped>
table {
  @apply text-left;
}

th,
td {
  @apply border border-cyan-600 border-opacity-80 p-2 bg-slate-900;
}

th {
  @apply bg-opacity-80;
}

td {
  @apply bg-opacity-20;
}
</style>
