<template>
  <div
    class="form max-w-2xl border bg-cyan-900 bg-opacity-10 border-cyan-700 mx-auto rounded-sm"
  >
    <h1 class="mt-5 text-3xl text-center text-cyan-300">Registration</h1>
    <div class="p-5 pt-3">
      <div class="form-control">
        <label for="email">E-Mail</label>
        <div class="input-container" :class="{ borderRed: emailError }">
          <span class="icon-container" :class="{ backgroundRed: emailError }">
            <i class="far fa-envelope"></i>
          </span>
          <input
            type="email"
            id="email"
            v-model="email"
            placeholder="example@gmail.com"
            @input="authenticateEmail"
          />
        </div>
        <p class="error">{{ emailError }}</p>
      </div>
      <div class="form-control">
        <label for="username">Username</label>
        <div class="input-container" :class="{ borderRed: usernameError }">
          <span
            class="icon-container"
            :class="{ backgroundRed: usernameError }"
          >
            <i class="fas fa-user"></i>
          </span>
          <input
            type="text"
            id="username"
            v-model="username"
            placeholder="Your username will be visible to others"
            @input="authenticateUsername"
          />
        </div>
        <p class="error">{{ usernameError }}</p>
      </div>
      <div class="form-control">
        <label for="password">Password</label>
        <div class="input-container" :class="{ borderRed: passwordError }">
          <span
            class="icon-container"
            :class="{ backgroundRed: passwordError }"
          >
            <i class="fas fa-lock"></i>
          </span>
          <input
            type="password"
            id="password"
            v-model="password"
            placeholder="Your password to sign in"
            @input="authenticatePassword"
          />
        </div>
        <p class="error" v-if="passwordError">{{ passwordError }}</p>
      </div>
      <div class="form-control">
        <label for="password-repeat">Password Repeat</label>
        <div
          class="input-container"
          :class="{ borderRed: passwordRepeatError }"
        >
          <span
            class="icon-container"
            :class="{ backgroundRed: passwordRepeatError }"
          >
            <i class="fas fa-key"></i>
          </span>
          <input
            type="password"
            id="password-repeat"
            v-model="passwordRepeat"
            placeholder="Confirm your password"
            @input="authenticatePasswordRepeat"
          />
        </div>
        <p class="error">
          {{ passwordRepeatError }}
        </p>
      </div>
      <div class="form-control text-center">
        <p class="error font-semibold">{{ error }}</p>
        <button
          class="p-2 px-3 w-full mt-6 rounded-sm bg-cyan-600 hover:bg-cyan-500 text-xl transition duration-100"
          :class="{ backgroundRed: formInvalid }"
          @click="insertUser"
        >
          Register account
        </button>
        <p class="mt-7 mb-2 text-lg">Already have an account?</p>
        <router-link to="/login">
          <button
            class="p-2 px-3 w-full rounded-sm border text-teal-400 border-teal-400 hover:border-teal-500 hover:text-teal-500 text-xl transition duration-100"
          >
            Sign in
          </button>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import users_controller from "../../controllers/users";
import router from "../router";

export default {
  name: "RegisterView",
  components: {},
  data() {
    return {
      email: "",
      username: "",
      password: "",
      passwordRepeat: "",
      emailError: "",
      usernameError: "",
      passwordError: "",
      passwordRepeatError: "",
      formInvalid: false,
      registerTries: 0,
      error: "",
    };
  },
  methods: {
    authenticateEmail() {
      if (
        // eslint-disable-next-line
        /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/.test(
          this.email
        ) == false &&
        this.email != ""
      ) {
        this.emailError = "E-Mail must be in the correct format";
      } else {
        this.emailError = "";
      }
      this.authenticateForm();
    },

    authenticateUsername() {
      if (
        /^[A-Za-z0-9 _]*[A-Za-z0-9][A-Za-z0-9 _]*$/.test(this.username) ==
          false &&
        this.username != ""
      ) {
        this.usernameError = "Username may only include letters and numbers";
      } else {
        this.usernameError = "";
      }
      this.authenticateForm();
    },

    authenticatePassword() {
      if (this.password.length < 5 && this.password != "") {
        this.passwordError = "Password must be at least 5 characters long";
      } else {
        this.passwordError = "";
      }
      this.authenticateForm();
    },

    authenticatePasswordRepeat() {
      if (this.password != this.passwordRepeat) {
        this.passwordRepeatError = "Passwords do not match";
      } else {
        this.passwordRepeatError = "";
      }
      this.authenticateForm();
    },

    authenticateForm() {
      if (this.registerTries >= 1) {
        if (
          this.email != "" &&
          this.username != "" &&
          this.password != "" &&
          this.passwordRepeat != "" &&
          this.emailError === "" &&
          this.usernameError === "" &&
          this.passwordError === "" &&
          this.passwordRepeatError === ""
        ) {
          this.formInvalid = false;
        } else {
          this.formInvalid = true;
        }
      }
    },

    async insertUser() {
      this.registerTries++;
      this.authenticateForm();
      if (this.formInvalid === false) {
        try {
          const response = await users_controller.insertUser(
            this.email,
            this.username,
            this.password
          );
          if (response.status === 201) {
            router.push("/login");
          } else {
            this.error = response;
          }
        } catch (error) {
          this.error = error.message;
        }
      }
    },
  },
};
</script>

<style scoped>
label {
  display: block;
  @apply text-lg mb-2;
}

.input-container {
  @apply my-1 rounded-sm  flex;
}

.icon-container {
  @apply p-2 px-3 text-lg bg-cyan-600;
}

input {
  @apply focus:outline-none p-2 text-lg w-full bg-slate-800;
}

.form-control {
  @apply my-4 w-full max-w-lg mx-auto;
}

.error {
  @apply text-red-700 mt-2;
}

.backgroundRed {
  @apply bg-red-700 hover:bg-red-700;
}

.borderRed {
  @apply border-red-700 focus:border-red-700;
}
</style>
