<template>
  <button class="selection" @click="toggle">
    {{ name + ": " + activeOptions }}
  </button>
  <div class="options" :class="{ block: active, hidden: !active }">
    <ul>
      <li
        class="flex"
        v-for="(option, index) in options"
        :item="option"
        :index="index"
        :key="index"
        :option="option"
      >
        <input
          class="form-checkbox mr-3 w-4 h-4 bg-cyan-300 border-0 focus:ring-0"
          type="checkbox"
          :id="option"
          :value="option"
          v-model="activeOptions"
          @change="updateInput"
        />
        <span class="checkmark"></span>
        <label class="block w-full" :for="option">{{ option }}</label>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "SelectionComponent",
  props: ["name", "options", "modelValue"],
  emits: ["update:modelValue"],
  data() {
    return {
      activeOptions: [],
      active: false,
    };
  },
  methods: {
    toggle() {
      this.active = !this.active;
    },
    updateInput() {
      this.$emit("update:modelValue", this.activeOptions);
    },
  },
};
</script>

<style scoped>
button {
  @apply mt-5 mb-2 p-2 w-full text-lg text-left border border-cyan-300 text-cyan-300 hover:bg-black hover:bg-opacity-20;
}

.selection {
  @apply relative;
}

.selection::after {
  content: "";
  border-top: 6px solid rgb(94, 234, 212);
  border-right: 6px solid transparent;
  border-bottom: 0 dotted;
  border-left: 6px solid transparent;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 15px;
}

.options {
  @apply w-full text-lg text-left border border-cyan-300 text-cyan-300;
}

li {
  @apply hover:bg-black hover:bg-opacity-20;
  display: block;
  position: relative;
}

label {
  @apply cursor-pointer ml-10 p-2;
}

input {
  position: absolute;
  left: 5px;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
  height: 15px;
  width: 15px;
  @apply border border-cyan-300;
}

li input:checked ~ .checkmark {
  @apply bg-cyan-300;
}

.checkmark::after {
  content: "";
  position: absolute;
  display: none;
}

li input:checked ~ .checkmark::after {
  display: block;
}

.checkmark::after {
  left: 7px;
  top: 3px;
  width: 3px;
  height: 8px;
  border: solid white;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
</style>
