import api from "../service/baseService";

class UsersController {
  static async insertUser(email, username, password) {
    try {
      const response = await api.post("users/create", {
        email,
        username,
        password,
      });
      return response;
    } catch (error) {
      if (error.response.status === 400) {
        return "Račun s to E-Pošto že obstaja";
      } else {
        return "Napaka v strežniku";
      }
    }
  }

  static async getUserDetails() {
    try {
      const response = await api.get("/users/details");

      return response.data;
    } catch (error) {
      return error;
    }
  }

  static async editUser(user) {
    try {
      const response = await api.put("users/update", user);
      return response;
    } catch (error) {
      return error.response;
    }
  }

  static async sendVerificationEmail() {
    try {
      const response = await api.post("/users/verify/send");

      return response.data;
    } catch (error) {
      return error;
    }
  }

  static async verifyEmail(verificationString) {
    try {
      const response = await api.get(
        "/users/verify/check/" + verificationString
      );

      return response.data;
    } catch (error) {
      return error;
    }
  }

  static async authenticateUser(email, password) {
    try {
      const response = await api.post("users/login", {
        email,
        password,
      });
      return response;
    } catch (error) {
      return error.response;
    }
  }
}

export default UsersController;
