<template>
  <h1 class="mt-5 text-3xl text-center text-teal-300">{{ phone.fullname }}</h1>
  <hr class="my-5 border-teal-300" />
  <div class="flex mt-10">
    <div class="w-1/2">
      <img class="w-64" :src="phone.image" alt="Image" />
    </div>
    <div class="w-1/2 text-center flex items-center">
      <div class="product-details" v-if="phone.stock > 0">
        <p class="text-4xl mb-5">{{ phone.price }} €</p>
        <p class="text-xl text-green-500 mb-5">
          {{ phone.stock }} products in stock
        </p>
        <button
          class="p-2 text-lg text-teal-300 border border-teal-300 font-semibold hover:text-teal-400 hover:border-teal-400 hover:scale-110 transition-all duration-200"
        >
          BUY NOW
        </button>
      </div>
      <div class="product-details" v-else>
        <p class="text-xl text-red-500 mb-5">Not in stock</p>
        <button
          v-if="!wished"
          class="p-2 text-lg text-cyan-300 border border-cyan-300 font-semibold hover:text-cyan-400 hover:border-cyan-400 hover:scale-110 transition-all duration-200"
          @click="addWish"
        >
          ADD TO WISHLIST
        </button>
        <button
          v-else
          class="p-2 text-lg text-cyan-300 border border-cyan-300 font-semibold hover:text-cyan-400 hover:border-cyan-400 hover:scale-110 transition-all duration-200"
          @click="removeWish"
        >
          REMOVE FROM WISHLIST
        </button>
      </div>
    </div>
  </div>
  <div class="my-10">
    <table
      class="w-full mb-2 border border-slate-600"
      v-for="(spec, index) in phone.specs"
      :item="spec"
      :index="index"
      :key="index"
      :spec="spec"
    >
      <tr>
        <th
          class="uppercase text-xl text-teal-300 w-1/5 border border-slate-600"
        >
          {{ spec.spec }}
        </th>
        <td class="w-4/5">
          <table class="w-full">
            <tr
              class="border-slate-600"
              v-for="(subspec, index) in spec.subspecs"
              :item="subspec"
              :index="index"
              :key="index"
              :subspec="subspec"
              :class="{ 'border-b': index != spec.subspecs.length - 1 }"
            >
              <th
                class="capitalize text-lg font-semibold text-cyan-300 w-1/5 border-r border-slate-600"
              >
                {{ subspec.subspec }}
              </th>
              <td class="w-4/5">{{ subspec.attribute }}</td>
            </tr>
          </table>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import PhonesController from "../../controllers/phones.js";
import { mapGetters } from "vuex";

export default {
  name: "PhonesView",
  data() {
    return {
      phone: {},
      wished: false,
      error: "",
    };
  },
  computed: {
    ...mapGetters({
      authenticated: "auth/authenticated",
    }),
  },
  methods: {
    async getPhoneDetails(phone) {
      try {
        this.phone = await PhonesController.getPhoneDetails(phone);

        if (this.phone.stock <= 0 && this.authenticated) {
          this.checkWish();
        }
      } catch (error) {
        this.error = error.message;
      }
    },
    async checkWish() {
      try {
        const response = await PhonesController.checkWish(this.phone._id);

        if (response.wished) {
          this.wished = true;
        } else {
          this.wished = false;
        }
      } catch (error) {
        this.error = error.message;
      }
    },
    async removeWish() {
      try {
        const response = await PhonesController.removeWish(this.phone._id);

        if (response.status === 403) {
          this.error = response;
        } else {
          this.checkWish();
        }
      } catch (error) {
        this.error = error.message;
      }
    },
    async addWish() {
      try {
        const response = await PhonesController.addWish(this.phone._id);

        if (response.status === 403) {
          this.error = response;
        } else if (response.status === 401) {
          this.error = "You must log in first";
        } else {
          this.checkWish();
        }
      } catch (error) {
        this.error = error.message;
      }
    },
  },
  beforeMount() {
    this.getPhoneDetails(this.$route.params.phone);
  },
};
</script>

<style scoped>
th {
  vertical-align: top;
  text-align: left;
  word-break: break-all;
}

th,
td,
table {
  @apply p-1;
}

.product-details {
  @apply bg-slate-900 bg-opacity-50 p-10 px-20 w-max mx-auto rounded-md;
}
</style>
