<template>
  <h1 class="mt-5 text-3xl text-center text-teal-300">Results</h1>
  <div class="list">
    <router-link
      v-for="(phone, index) in phones"
      :item="phone"
      :index="index"
      :key="index"
      :phone="phone"
      :to="'/phone/' + phone.url.split('/')[3].split('.')[0]"
    >
      <div class="card">
        <div>
          <img :src="phone.image" alt="Image" class="mx-auto" />
        </div>
        <div class="mt-2 text-center text-lg break-words">
          <p>{{ phone.brand }}</p>
          <p>{{ phone.model }}</p>
        </div>
      </div>
    </router-link>
  </div>
</template>

<script>
import PhonesController from "../../controllers/phones.js";

export default {
  name: "ResultsView",
  data() {
    return {
      phones: [],
      error: "",
    };
  },
  methods: {
    async getPhones(params) {
      try {
        this.phones = await PhonesController.getPhones(params);
      } catch (error) {
        this.error = error.message;
      }
    },
  },
  beforeMount() {
    this.getPhones(this.$route.query);
  },
};
</script>

<style scoped>
.list {
  @apply grid my-10 lg:grid-cols-5 md:grid-cols-4 sm:grid-cols-3 gap-4 grid-flow-row place-content-center;
}

.card {
  @apply w-52 py-4 border border-slate-800  bg-slate-700 bg-opacity-30 rounded-lg shadow-sm hover:bg-black hover:bg-opacity-20 hover:text-cyan-300 transition-all duration-150;
}

.card img,
.card p {
  @apply opacity-90;
}

.card:hover {
  transform: translate(7px, -10px);
}

.card:hover img,
.card:hover p {
  @apply opacity-100;
}
</style>
