<template>
  <details open>
    <summary class="mt-5 text-2xl text-cyan-300 cursor-pointer" open>
      Owned phones
    </summary>
    <div class="list">
      <router-link
        v-for="(phone, index) in phones"
        :item="phone"
        :index="index"
        :key="phone._id"
        :phone="phone"
        :to="'/phone/' + phone.url"
      >
        <div class="card hover:text-cyan-300">
          <div>
            <img :src="phone.image" alt="Image" class="mx-auto" />
          </div>
          <div class="mt-2 text-center text-lg break-words">
            <p>{{ phone.brand }}</p>
            <p>{{ phone.model }}</p>
          </div>
        </div>
      </router-link>
    </div>
  </details>

  <details open>
    <summary class="mt-5 text-2xl text-teal-300 cursor-pointer">
      Wished phones
    </summary>
    <div class="list">
      <router-link
        v-for="(phone, index) in wishes"
        :item="phone"
        :index="index"
        :key="phone._id"
        :phone="phone"
        :to="'/phone/' + phone.url"
      >
        <div class="card">
          <div>
            <img :src="phone.image" alt="Image" class="mx-auto" />
          </div>
          <div class="mt-2 text-center text-lg break-words">
            <p>{{ phone.brand }}</p>
            <p>{{ phone.model }}</p>
          </div>
        </div>
      </router-link>
    </div>
  </details>
</template>

<script>
import PhonesController from "../../controllers/phones.js";

export default {
  name: "ResultsView",
  data() {
    return {
      phones: [],
      wishes: [],
      error: "",
    };
  },
  methods: {
    async getPhones() {
      try {
        this.wishes = await PhonesController.getWishes();
      } catch (error) {
        this.error = error.message;
      }
    },
  },
  beforeMount() {
    this.getPhones(this.$route.query);
  },
};
</script>

<style scoped>
.list {
  @apply grid my-10 lg:grid-cols-5 md:grid-cols-4 sm:grid-cols-3 gap-4 grid-flow-row place-content-center;
}

.card {
  @apply w-52 py-4 border border-slate-800  bg-slate-700 bg-opacity-30 rounded-lg shadow-sm hover:bg-black hover:bg-opacity-20 hover:text-teal-300 transition-all duration-150;
}

.card img,
.card p {
  @apply opacity-90;
}

.card:hover {
  transform: translate(7px, -10px);
}

.card:hover img,
.card:hover p {
  @apply opacity-100;
}
</style>
