<template>
  <div
    class="form max-w-2xl border border-opacity-90 bg-cyan-900 bg-opacity-10 border-teal-700 mx-auto rounded-md"
  >
    <h1 class="mt-5 text-3xl text-center text-teal-300">Sign in</h1>
    <div class="p-10 pt-3">
      <div class="form-control">
        <label for="email">E-Mail</label>
        <div class="input-container" :class="{ borderRed: loginError }">
          <span class="icon-container" :class="{ backgroundRed: loginError }">
            <i class="far fa-envelope"></i>
          </span>
          <input
            type="email"
            id="email"
            v-model="email"
            placeholder="example@gmail.com"
          />
        </div>
      </div>
      <div class="form-control">
        <label for="password">Password</label>
        <div class="input-container" :class="{ borderRed: loginError }">
          <span class="icon-container" :class="{ backgroundRed: loginError }">
            <i class="fas fa-lock"></i>
          </span>
          <input
            type="password"
            id="password"
            v-model="password"
            placeholder="Your password"
          />
        </div>
      </div>
      <div class="form-control text-center">
        <p class="error">{{ loginError }}</p>
        <p class="error">{{ loginResponse }}</p>
        <button
          class="p-2 px-3 w-full mt-6 rounded-sm bg-teal-700 hover:bg-teal-600 text-xl transition duration-100"
          @click="loginCheck"
        >
          Sign in
        </button>
        <p class="mt-7 mb-2 text-lg">New user?</p>
        <router-link to="/register">
          <button
            class="p-2 px-3 w-full rounded-sm border text-cyan-400 border-cyan-400 hover:border-cyan-500 hover:text-cyan-500 text-xl transition duration-100"
          >
            Register account
          </button>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import users_controller from "../../controllers/users";
import router from "../router";

export default {
  name: "LoginView",
  components: {},
  data() {
    return {
      email: "",
      password: "",
      loginError: "",
      loginResponse: "",
    };
  },
  methods: {
    ...mapActions({
      saveToken: "auth/saveToken",
    }),

    async loginCheck() {
      if (this.email != "" && this.password != "") {
        const response = await users_controller.authenticateUser(
          this.email,
          this.password
        );
        if (response.status !== 200) {
          this.loginResponse = response.data;
        } else {
          await this.saveToken(response.data.accessToken);
          router.push("/profile");
        }
      } else {
        this.loginResponse = "Please fill in both fields";
      }
    },
  },
};
</script>

<style scoped>
label {
  display: block;
  @apply text-lg mb-2;
}

.input-container {
  @apply my-1 rounded-sm  flex;
}

.icon-container {
  @apply p-2 px-3 text-lg bg-teal-600;
}

input {
  @apply focus:outline-none p-2 text-lg w-full bg-slate-800;
}

.form-control {
  @apply my-4 w-full max-w-lg mx-auto;
}

.error {
  @apply text-red-700 mt-2;
}

.backgroundRed {
  @apply bg-red-700 hover:bg-red-700;
}

.borderRed {
  @apply border-red-700 focus:border-red-700;
}
</style>
