import { createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";
import LoginView from "../views/LoginView.vue";
import RegisterView from "../views/RegisterView.vue";
import SearchView from "../views/SearchView.vue";
import ResultsView from "../views/ResultsView.vue";
import PhoneView from "../views/PhoneView.vue";
import ProfileView from "../views/ProfileView.vue";
import ProfileEditView from "../views/ProfileEditView.vue";
import VerifyView from "../views/VerifyView.vue";
import AdminView from "../views/AdminView.vue";
import StockView from "../views/StockView.vue";
import StockAddView from "../views/StockAddView.vue";
import UserPhonesView from "../views/UserPhonesView.vue";
import WishesView from "../views/WishesView.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/login",
    name: "login",
    component: LoginView,
    beforeEnter: (to, from, next) => {
      if (localStorage.getItem("token")) {
        return next({
          name: "profile",
        });
      }
      next();
    },
  },
  {
    path: "/register",
    name: "register",
    component: RegisterView,
    beforeEnter: (to, from, next) => {
      if (localStorage.getItem("token")) {
        return next({
          name: "profile",
        });
      }
      next();
    },
  },
  {
    path: "/search",
    name: "search",
    component: SearchView,
  },
  {
    path: "/results",
    name: "results",
    component: ResultsView,
  },
  {
    path: "/phone/:phone",
    name: "phone",
    component: PhoneView,
  },
  {
    path: "/profile",
    name: "profile",
    component: ProfileView,
    beforeEnter: (to, from, next) => {
      if (!localStorage.getItem("token")) {
        return next({
          name: "login",
        });
      }
      next();
    },
  },
  {
    path: "/myphones",
    name: "myphones",
    component: UserPhonesView,
    beforeEnter: (to, from, next) => {
      if (!localStorage.getItem("token")) {
        return next({
          name: "login",
        });
      }
      next();
    },
  },
  {
    path: "/profile/edit",
    name: "profileEdit",
    component: ProfileEditView,
    beforeEnter: (to, from, next) => {
      if (!localStorage.getItem("token")) {
        return next({
          name: "login",
        });
      }
      next();
    },
  },
  {
    path: "/user/verify/:verificationString",
    name: "verify",
    component: VerifyView,
    beforeEnter: (to, from, next) => {
      if (!localStorage.getItem("token")) {
        return next({
          name: "login",
        });
      }
      next();
    },
  },
  {
    path: "/admin",
    name: "admin",
    component: AdminView,
    beforeEnter: (to, from, next) => {
      if (!localStorage.getItem("token")) {
        return next({
          name: "login",
        });
      }
      next();
    },
  },
  {
    path: "/admin/stock",
    name: "stock",
    component: StockView,
    beforeEnter: (to, from, next) => {
      if (!localStorage.getItem("token")) {
        return next({
          name: "login",
        });
      }
      next();
    },
  },
  {
    path: "/admin/wishes",
    name: "wishes",
    component: WishesView,
    beforeEnter: (to, from, next) => {
      if (!localStorage.getItem("token")) {
        return next({
          name: "login",
        });
      }
      next();
    },
  },
  {
    path: "/admin/stock/add",
    name: "stockadd",
    component: StockAddView,
    beforeEnter: (to, from, next) => {
      if (!localStorage.getItem("token")) {
        return next({
          name: "login",
        });
      }
      next();
    },
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
