<template>
  <h1 class="text-3xl text-cyan-300 text-center border-b border-cyan-400 pb-3">
    Admin Panel
  </h1>
  <div class="my-10 grid md:grid-cols-3 sm:grid-cols-2 gap-5 px-2">
    <router-link to="/admin/stock">
      <button><i class="fas fa-boxes"></i>Phones Stock</button>
    </router-link>
    <router-link to="/admin/wishes">
      <button><i class="fas fa-mobile-alt"></i>Wished Phones</button>
    </router-link>
    <router-link to="/admin/tickets">
      <button><i class="fas fa-comment"></i>User Problems</button>
    </router-link>
    <router-link to="/admin/users">
      <button><i class="fas fa-users"></i>Users</button>
    </router-link>
    <router-link to="/">
      <button><i class="fas fa-home"></i>Home</button>
    </router-link>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import router from "../router";

export default {
  name: "AdminView",
  components: {},
  computed: {
    ...mapGetters({
      authenticated: "auth/authenticated",
      user: "auth/user",
      isAdmin: "auth/isAdmin",
    }),
  },
  data() {
    return {};
  },
  beforeMount() {
    if (!this.isAdmin) {
      router.push("/login");
    }
  },
};
</script>

<style scoped>
button {
  @apply w-full py-10 text-xl bg-black bg-opacity-10 border border-teal-500 text-teal-500 hover:scale-105 hover:border-cyan-300 hover:text-cyan-300 transition-all duration-150;
}

i {
  @apply block text-3xl mb-2;
}
</style>
