<template>
  <div
    class="form p-5 max-w-2xl border bg-cyan-900 bg-opacity-10 border-cyan-700 mx-auto rounded-sm"
  >
    <h1 class="text-3xl mb-5 text-center text-cyan-300">Edit profile</h1>
    <label for="email"
      >Email (Email verification will be reset if changed)</label
    >
    <input type="text" id="email" v-model="user.email" />
    <label for="username">Username</label>
    <input type="text" id="username" v-model="user.username" />

    <p v-if="notice != ''" class="text-green-500 text-center text-lg mt-5">
      {{ notice }}
    </p>
    <p v-if="error != ''" class="text-red-500 text-center text-lg mt-5">
      {{ error }}
    </p>
    <div class="profile-buttons">
      <router-link to="/profile">
        <button>Back</button>
      </router-link>
      <button @click="editUser">Edit</button>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import UsersController from "../../controllers/users.js";
import router from "../router";

export default {
  name: "ProfileView",
  components: {},
  data() {
    return {
      user: {},
      error: "",
      notice: "",
    };
  },
  methods: {
    ...mapActions({
      saveToken: "auth/saveToken",
    }),

    async getUser() {
      try {
        this.user = await UsersController.getUserDetails();
      } catch (error) {
        this.error = error;
      }
    },

    async editUser() {
      try {
        const response = await UsersController.editUser({
          email: this.user.email,
          username: this.user.username,
        });
        if (response.data.accessToken) {
          await this.saveToken(response.data.accessToken);
          this.notice = response.data;
          router.push("/profile");
        } else {
          this.error = response.data;
        }
      } catch (error) {
        this.error = error;
      }
    },
  },
  beforeMount() {
    this.getUser();
  },
};
</script>

<style scoped>
input {
  @apply my-3 px-3 py-2 w-full text-lg bg-transparent border border-cyan-300 focus:outline-none outline-none focus:border-teal-300 transition-all duration-150;
}

label {
  @apply text-xl text-cyan-300;
}

.profile-buttons {
  @apply flex flex-row flex-wrap justify-around mt-10;
}

.profile-buttons button {
  @apply w-32 m-2 text-center uppercase text-lg py-2 px-3 border border-teal-300 text-teal-300 hover:scale-105 hover:border-teal-500 hover:text-teal-500 transition-all duration-200;
}
</style>
