<template>
  <div
    class="form p-5 max-w-2xl border bg-cyan-900 bg-opacity-10 border-cyan-700 mx-auto rounded-sm"
  >
    <h1 class="text-3xl mb-5 text-center text-cyan-300">Email Verification</h1>
    <p v-if="notice != ''" class="text-green-500 text-center text-lg mt-5">
      {{ notice }}
    </p>
    <p v-if="error != ''" class="text-red-500 text-center text-lg mt-5">
      {{ error }}
    </p>
    <router-link to="/profile">
      <p class="text-center mt-5">Back to profile</p>
    </router-link>
  </div>
</template>

<script>
import UsersController from "../../controllers/users.js";

export default {
  name: "ProfileView",
  components: {},
  data() {
    return {
      error: "",
      notice: "",
    };
  },
  methods: {
    async verify(verificationString) {
      try {
        this.notice = await UsersController.verifyEmail(verificationString);
      } catch (error) {
        this.error = error;
      }
    },
  },
  beforeMount() {
    this.verify(this.$route.params.verificationString);
  },
};
</script>

